<template>
  <v-row>
    <v-col>
      <v-toolbar flat>
        <v-toolbar-title>
          <v-btn color="warning" text plain to="/users">Index</v-btn>
          /<span class="text--disabled ml-3 subtitle-1">{{ $route.name }}</span>
        </v-toolbar-title>
        <v-divider class="mx-4" inset vertical></v-divider>
        <v-spacer></v-spacer>
        <v-col cols="12" md="3">
          <v-menu
            ref="menu"
            v-model="menu"
            :close-on-content-click="false"
            :return-value.sync="date"
            transition="scale-transition"
            offset-y
            min-width="auto"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                v-model="date"
                label="Select Log Date"
                prepend-icon="mdi-calendar"
                readonly
                v-bind="attrs"
                v-on="on"
              ></v-text-field>
            </template>
            <v-date-picker v-model="date" no-title scrollable range>
              <v-spacer></v-spacer>
              <v-btn text color="primary" @click="menu = false"> Cancel </v-btn>
              <v-btn text color="primary" @click="searchForDate()">
                OK
              </v-btn>
            </v-date-picker>
          </v-menu>
        </v-col>

        <dm-input
          v-model="searchKey"
          @keyup="search()"
          label="Search"
        ></dm-input>
      </v-toolbar>
      <v-simple-table fixed-header :height="'calc(100vh - 136px)'">
        <thead>
          <tr>
            <th v-for="header in headers" :key="header.value">
              <v-select
                v-model="filterValue[header.value]"
                v-if="header.searchable"
                multiple
                :items="filterData[header.value]"
              >
                <template v-slot:label>
                  <h5>{{ header.text }}</h5>
                </template>
                <!--                <template v-slot:selection="{item}">-->
                <!--                  <h5>{{ item }}</h5>-->
                <!--                </template>-->
              </v-select>
              <template v-else>{{ header.text }}</template>
            </th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item in items" :key="item.timestamp">
            <td>{{ longToDate(item.arrivalTime) }}</td>
            <!--          <td>{{ item.deviceId }}</td>-->
            <td>{{ item.log.dst_ip }}</td>
            <td>
              <span class="red--text text--lighten-1">
                {{ item.log.dst_port }}
              </span>
            </td>
            <td>{{ item.log.src_ip }}</td>
            <td>
              <span class="orange--text text--lighten-1">
                {{ item.log.src_port }}
              </span>
            </td>
            <td>
              <v-chip class="ma-2" color="primary">
                {{ item.log.data_type }}
              </v-chip>
            </td>
            <td>{{ item.log.event_id }}</td>
            <td style="width: 550px" class="text-break">
              {{ item.log.request }}
            </td>
            <td>{{ item.log.response }}</td>
          </tr>
          <tr v-intersect="onIntersect"></tr>
        </tbody>
      </v-simple-table>
    </v-col>
  </v-row>
</template>

<script>
import DmInput from "@/components/common/form/DmInput";

export default {
  name: "logs",
  components: { DmInput },
  data: () => ({
    headers: [
      { text: "Date", value: "arrivalTime", width: "80", searchable: false },
      { text: "Device Ip", value: "log.dst_ip", searchable: true },
      { text: "Device Port", value: "log.dst_port", searchable: true },
      { text: "Attacker Ip", value: "log.src_ip", searchable: true },
      { text: "Attacker Port", value: "log.src_port", searchable: true },
      { text: "Service", value: "log.data_type", searchable: true },
      { text: "Attack Session", value: "log.event_id", searchable: true },
      { text: "Request", value: "log.request", searchable: false },
      { text: "Response", value: "log.response", searchable: false },
    ],
    items: [],
    filterData: {},
    filterValue: {},
    totalLength: 0,
    searchKey: "",
    page: 1,
    timer: -1,
    menu : "",
    date : [],
  }),
  watch: {
    filterValue: {
      deep: true,
      handler: function () {
        this.page = 1;
        this.fetchItems();
      },
    },
  },
  computed: {},
  methods: {
    fetchItems() {
      let input = {};
      input.page = this.page;
      input.search = this.searchKey;
      input.dateRange = this.date;
      Object.keys(this.filterValue).forEach((key) => {
        if (Object.keys(this.filterValue[key]).length <= 0) {
          delete this.filterValue[key];
        }
      });
      if (Object.keys(this.filterValue).length > 0) {
        input.filter = this.filterValue;
      }
      this.$axios
        .post("secured/fetchLogs", input)
        .then(({ data }) => {
          this.items = data.result;
          this.totalLength = data.total;
        })
        .catch(({ response }) => {
          this.$toast.setMessage(response.data, 1);
        });
    },
    onIntersect(entries, observer) {
      if (entries[0].isIntersecting && this.items.length < this.totalLength) {
        this.page++;
        this.fetchItems();
      }
    },
    longToDate(milis) {
      var d = new Date(milis);
      return d.toLocaleString();
    },
    search() {
      if (this.timer < 0) {
        this.timer = setInterval(() => {
          this.doSearch();
        }, 1000);
      } else {
        clearInterval(this.timer);
        this.timer = setInterval(() => {
          this.doSearch();
        }, 1000);
      }
    },
    doSearch() {
      this.page = 1;
      this.fetchItems();
      clearInterval(this.timer);
    },
    getFilterData() {
      this.$axios
        .post("secured/getFilterData")
        .then(({ data }) => {
          this.filterData = data;
          this.fetchItems();
        })
        .catch(({ response }) => {
          this.$toast.setMessage(response.data, 1);
        });
    },
    searchForDate(){
      this.$refs.menu.save(this.date);
      this.fetchItems();
    }
  },
  created() {},
  beforeMount() {
    this.getFilterData();
  },
};
</script>
